import {
    Alert,
	Card,
	CardContent,
	Divider,
	Grid,
	styled,
	Typography,
} from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";
import { useTranslation } from "react-i18next";

import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";

import RevisitMessageForm from "./RevisitMessageForm";
import WelcomeMessageForm from "./WelcomeMessageForm";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const Welcome = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const task = useTask("Customers-Club.welcome");

	const GUIDE = `
### نکات آموزشی

1- متن قسمت پیامک خوش آمد گویی در اولین باری که مشتری شماره خود را وارد دستگاه دایرکت می کند بلافاصله برای او پیامک می شود.

2- برای دفعه دوم و دفعات بعدی که مشتری شماره خود را وارد دستگاه دایرکت می کند متن قسمت تکرار خرید برای مشتری پیامک می شود.

3- می توانید این دو متن را یکسان قرار دهید یا با هم متفاوت باشند.

4- اگر متن قسمت تکرار خرید خالی باشد فقط اولین باری که شماره وارد دستگاه می شود پیامک خوش آمدگویی را دریافت می کند و در دفعات بعدی پیامکی ارسال نمی شود.

5- پیامک خوش آمدگویی و تکرار خرید به همه حتی کسانی که تبلیغات را مسدود کرده اند ارسال می شود.

کلیه پیامک های این قسمت در بازه زمانی 8 صبح تا 11 شب ارسال می شوند ، مثال اگر مشتری ساعت 10 شب شماره اش را وارد دستگاه کند و شما تعریف کرده باشید که 2 ساعت بعد پیامک نظرسنجی برای او ارسال شود ، پیامک ساعت 8 صبح روز بعد برای مشتری ارسال می شود.
`;

	return (
		<TabView {...task}>
			<ResponsiveContainer>
				<Card variant="outlined" sx={{ mb: 3 }}>
					<CardContent sx={{ p: 3 }}>
						<Grid container spacing={3}>
							<Grid item xs={12} md={6}>
                                <Typography variant="h6" mb={1}>{t("pages.welcome.welcome_message_title")}</Typography>
                                <Typography color="text.secondary" mb={1}>{t("pages.welcome.welcome_message_description")}</Typography>

								<WelcomeMessageForm />

								<Divider sx={{ my: 3 }} />

                                <Typography variant="h6" mb={1}>{t("pages.welcome.revisit_message_title")}</Typography>
                                <Typography color="text.secondary" mb={1}>{t("pages.welcome.revisit_message_description")}</Typography>

								<RevisitMessageForm />
							</Grid>
							<Grid item xs={12} sm={6}>
                                <Alert color="info">
                                    <MarkdownContainer>
                                        <Markdown breaks={true}>
                                            {DOMPurify.sanitize(GUIDE)}
                                        </Markdown>
                                    </MarkdownContainer>
                                </Alert>
							</Grid>
						</Grid>
					</CardContent>
				</Card>
			</ResponsiveContainer>
		</TabView>
	);
};

export default Welcome;
