import {
	Alert,
	AlertTitle,
	Box,
	Button,
	Chip,
	Collapse,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	Switch,
} from "@mui/material";
import get from "lodash/get";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import { useMiddleware } from "../../../../Application";
import useForm from "../../../../utils/useForm";
import SmsField from "../../components/SmsField";
import QuantityInput from "../../../../components/QuantityInput";

const RevisitMessageForm = () => {
	const { t } = useTranslation(["customers-club", "global"]);

	const { enqueueSnackbar } = useSnackbar();

	const middleware = useMiddleware("Customers-Club.Welcome");

	const statusVariants = {
		pending: {
			type: "pending",
			color: "warning",
			label: t("global:general.pending"),
			text: t("pages.welcome.welcome_pending_text"),
		},
		confirmed: {
			type: "confirmed",
			color: "success",
			label: t("global:general.active"),
			text: t("pages.welcome.revisit_active_text"),
		},
		rejected: {
			type: "rejected",
			color: "error",
			label: t("global:general.inactive"),
			text: t("pages.welcome.welcome_inactive_text"),
		},
	};

	const [status, setStatus] = useState();
	const [initialValues, setInitialValues] = useState({
		revisit_content: "",
		revisit_active: true,
		revisit_delay: 1,
	});

	const { register, setValue, getValue, reset, loading, handleSubmit } =
		useForm({
			initialValues,
			handlePost: (data) => middleware.updateOptions(data),
			onSubmit: (response) => {
				enqueueSnackbar(
					t("global:general.update_successful", "Update successful")
				);
				// setInitialValues(response.options);
				getOptions();
			},
		});
	const getOptions = async () => {
		const response = await middleware.getOptions();
		setInitialValues({
			revisit_content: response.data.revisit_message?.content,
			revisit_active: response.data.revisit_active,
			revisit_delay: response.data.revisit_delay,
		});
		setStatus(get(statusVariants, response.data.revisit_message?.status));
	};

	useEffect(() => {
		getOptions();
	}, []);

	useEffect(() => {
		reset(initialValues);
	}, [initialValues]);

	return (
		<Box>
			<Collapse in={["pending", "rejected"].includes(status?.type)}>
				<Alert
					icon={false}
					variant="standard"
					severity={status?.color}
					action={
						status?.type === "pending" && (
							<Chip color={status?.color} label={status?.label} />
						)
					}
					sx={{ mb: 2 }}
				>
					<AlertTitle>{t("global:general.status")}</AlertTitle>
					{status?.text}
				</Alert>
			</Collapse>

			<Box>
				<SmsField
					disabled={
						status?.type === "pending" ||
						(status?.type === "confirmed" &&
							!Boolean(getValue("revisit_active")))
					}
					{...register("revisit_content", [
						"helperText",
						"FormHelperTextProps",
						"error",
					])}
					onChange={(value) => setValue("revisit_content", value)}
				/>
			</Box>

			<Box sx={{ my: 2 }} />

			<ListItem disableGutters disablePadding>
				<ListItemButton
					selected
					disabled={status?.type !== "confirmed"}
					onClick={() =>
						setValue("revisit_active", !getValue("revisit_active"))
					}
				>
					<ListItemText
						primary={t("ارسال فعال")}
						secondary={t(
							"در صورت غیر فعال بودن، پیام ارسال نخواهد شد."
						)}
					/>
					<ListItemIcon>
						<Switch
							checked={getValue("revisit_active")}
							onChange={(e) =>
								setValue("revisit_active", e.target.checked)
							}
						/>
					</ListItemIcon>
				</ListItemButton>
			</ListItem>

			<Box sx={{ my: 1 }} />

			<ListItem disableGutters disablePadding>
				<ListItemButton
					selected
					disabled={status?.type !== "confirmed"}
				>
					<ListItemText
						primary={t("تاخیر ارسال")}
						secondary={t(
							"متن پیام چند ساعت پس از رجوع به مشتری پیامک گردد؟"
						)}
					/>
					<ListItemIcon>
						<QuantityInput
							min={0}
							max={14}
							step={1}
							value={getValue("revisit_delay")}
							onChange={(e, value) =>
								setValue("revisit_delay", value)
							}
						/>
					</ListItemIcon>
				</ListItemButton>
			</ListItem>

			<Box sx={{ my: 2 }} />

			<Button
				variant="contained"
				onClick={handleSubmit}
				disabled={loading}
			>
				{t("global:general.save_changes")}
			</Button>
		</Box>
	);
};

export default RevisitMessageForm;
